*,
*:before,
*:after {
  box-sizing: border-box;
}

body {
  margin: 0;
}

.App {
  display: block;

  width: 100%;
}

.container {
  width: 1200px;

  margin: 0 auto;
}

.jokes-container {
  display: flex;
  flex-direction: column;

  text-align: center;
}

li {
  list-style-type: none;
}

.joke {
  margin-top: 10px;

  padding: 15px;

  text-align: left;

  border: 5px solid black;
}